
import { Entity, engine } from '@dcl/sdk/ecs';
import { Quaternion, Vector3 } from '@dcl/sdk/math';
import *  as  npc from 'dcl-npc-toolkit'
import { questProgress } from './events'
import { catIsOut, spawnCat } from './cat';
import { StepsEnum } from './shared/questMessages';
import * as utils from '@dcl-sdk/utils'
import { placeInHand } from './drink'
import { sendQuestAction, getQuestState } from './client/questClient'
import { isServer } from '@dcl/sdk/network'

let octo: Entity
export let catGuy: Entity | null = null

export function addNPCs() {
  // NPCs should only be created on client side
  if (isServer()) {
    return
  }

  questProgress.on("step", (stepNumber: StepsEnum) => {
    const currentState = getQuestState()
    console.log("QUEST, NEW STEP NUMBER: ", stepNumber, "Current state:", currentState.currentStep)

    if (catIsOut && stepNumber === StepsEnum.catGuy_step) {
      npc.talk(catGuy!, catQuest, `collect`)
    }
  })


  octo = npc.create(
    { position: Vector3.create(79.28, 0, 135.34), rotation: Quaternion.fromEulerDegrees(0, -90, 0) },
    {
      type: npc.NPCType.CUSTOM,
      model: 'assets/models/NPCs/BobOctorossV46.glb',
      onActivate: () => {
        console.log("quest: ACTIVATED NPC")
        npc.playAnimation(octo, 'TalkIntro', true, 0.63)
        npc.changeIdleAnim(octo, 'TalkLoop')
        const questState = getQuestState()
        switch (questState.currentStep) {
          case StepsEnum.not_started:
            npc.talk(octo, OctoQuest, 'questQ')
            break
          case StepsEnum.talk_octo_1_step:
            npc.talk(octo, OctoQuest, 'noHairs')
            break
          case StepsEnum.catGuy_step:
            npc.talk(octo, OctoQuest, 'quest2')
            break
          case StepsEnum.talk_octo_2_step:
            npc.talk(octo, OctoQuest, 'noHerbs')
            break
          case StepsEnum.collect_herbs:
            npc.talk(octo, OctoQuest, 'quest3')
            break
          case StepsEnum.talk_octo_3_step:
            npc.talk(octo, OctoQuest, 'noCalis')
            break
          case StepsEnum.calis_step:
            npc.talk(octo, OctoQuest, 'makeDrink')
            break
          case StepsEnum.talk_octo_4_step:
            npc.talk(octo, OctoQuest, 'done')
            break
        }
      },
      portrait: `images/portraits/bartender.png`,
      //dialogSound: `sounds/navigationForward.mp3`,
      idleAnim: 'Idle',
      faceUser: false,
      reactDistance: 8,
      onWalkAway: () => {
        backToIdle()
      },
    }
  )

  catGuy = engine.getEntityOrNullByName("catguy")
  if (catGuy) {
    console.log('catguy entity found');
    npc.createFromEntity(catGuy, {
      type: npc.NPCType.CUSTOM,
      model: 'assets/models/NPCs/cat_guySittedV12.glb',
      reactDistance: 20,
      idleAnim: `idle`,
      faceUser: false,
      hoverText: "Talk",
      coolDownDuration: 3,
    
      //dialogSound: `sounds/navigationForward.mp3`,
      onActivate: () => {
        console.log('npc activated');
        const questState = getQuestState()
        if (questState.currentStep === StepsEnum.talk_octo_1_step) {
          npc.talk(catGuy!, catQuest)
          npc.playAnimation(catGuy!, `talk`)
        } else{
          console.log('npc not activated, current step: ', questState.currentStep);
        }
      },
      onWalkAway: () => {
        npc.playAnimation(catGuy!, `idle`)
      }
    })

  } else {
    console.log('catguy entity not found');
  }


}

export function backToIdle() {
  npc.changeIdleAnim(octo, 'Idle')
  npc.playAnimation(octo, 'TalkOutro', true, 0.63)
}


